import { createStore } from "vuex";
import axios from 'axios';

//     
// props emit  .
const stores= createStore({

    //   
    //   X, session storage (   )
    state : {
        menu   : sessionStorage.getItem("CURL"), //  
        logged : false, //  
        uid : '', //    
        uname : '', //   
        token : '', //     
        urole : ''
    },

    //  (getter)
    getters : {
        getUrole(state) {
            return state.urole;
        },
        getUid(state) {
            return state.uid;
        },
        getUname(state){
            return state.uname;
        },
        getMenu(state) {
            return state.menu;
        },
        getLogged(state) {
            return state.logged;
        }
    },

    //  (mutation) :  
    // value =>   .      
    // ex) (state, value, value ...)
    mutations : {
        setUrole(state, value) {
            console.log('store/setUrole', value); //  
            state.urole = value;
        },
        setUid(state, value) {
            state.uid = value;
        },
        setUname(state, value) {
            state.uname = value;
        },
        setMenu(state, value){
            state.menu = value;
        },
        setLogged(state, value) {
            state.logged = value;
        }
    },

    //  (action) :   
    //    
    // F5  
    actions : {
        async handleData(context, payload){
            console.log(payload);
            const token = sessionStorage.getItem("TOKEN");
            if(typeof token !== 'undefined' && token !== null) {
                const url = `/member/validation`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : token
                }
                const response = await axios.get(url, {headers:headers});
                console.log('store/index.js/handleData', response.data);
                if(response.data.status === 200) {
                    // mutations setUid, setUname  
                    context.commit("setUid", response.data.uid);
                    context.commit("setUname", response.data.uname);
                    context.commit("setUrole", response.data.urole);
                    context.commit("setLogged", true);
                }
                else{
                    //      (, ,   ..)
                    context.commit("setLogged", false);
                }
            }

        }

    }
});

export default stores;