<template>
    <div>
        <h3>Menu3/</h3>
        <el-form :inline="false" :model="state" class="demo-form-inline" style="width:500px">
            <el-form-item label=" ">
            <el-input v-model="state.pw"></el-input>
            </el-form-item>
        </el-form>
        
        <el-button type="primary" round @click="handleDeleteAction"> </el-button>

    </div>
</template>

<script>
import axios from 'axios';
import { reactive } from '@vue/reactivity';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

    export default {
        setup () {

            const router = useRouter();
            const store = useStore();

            const state = reactive ({
                pw : '',
                token : sessionStorage.getItem("TOKEN")
            })

            
            const handleDeleteAction = async() => {
                if(confirm(' ?')) {
                const url = `/member/delete`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                };
                const body = {
                    password : state.pw
                }

                // delete > delete(url, {headers:, data:})
                const response = await axios.delete(
                    url, {headers:headers, data:body});
                
                console.log(response.data);

                if(response.data.status===200) {
                    sessionStorage.removeItem("TOKEN");
                    alert(' .');
                    // 1.  
                    router.push({name:'Home'});
                    // 2.  
                    store.commit("setMenu", "/");
                    // 3.  
                    store.commit("setLogged", false);
                }
            }
        }

            return { state, handleDeleteAction }
        }
        
    }
</script>

