<template>
    <div>
        <h3>Menu1/</h3> 
    </div>
    <!--     -->
    <el-form :inline="false" :model="state" class="demo-form-inline" style="width:500px">
            <el-form-item label=" ">
            <el-input v-model="state.name"></el-input>
            </el-form-item>
        </el-form>

        <el-button type="primary" round @click="handleUpdateAction"></el-button>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';

    export default {
        setup () {
            const state = reactive ({
                name : '',
                token : sessionStorage.getItem("TOKEN")
            });
            
            const handleUpdateAction = async () => {
                if(state.token !== null) { //  
                const url = `/member/update`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                    };
                const body = {
                    name : state.name
                }
                const response = await axios.put(url, body, {headers:headers});
                console.log(response.data);
                if(response.data.status === 200){
                    alert(' ');
                }
            }
            
            else{
                //   
                // URL  
                //  /
                //   false
            }
        }

            return {state, handleUpdateAction}

        }
        
    }
</script>

