<template>
    <div>
        <h3>Menu2/</h3>
        <el-form :inline="false" :model="state" class="demo-form-inline" style="width:500px">
            <el-form-item label=" ">
            <el-input v-model="state.pw"></el-input>
            </el-form-item>
            <el-form-item label=" ">
            <el-input v-model="state.pw1"></el-input>
            </el-form-item>
            <el-form-item label="  ">
            <el-input v-model="state.pw1"></el-input>
            </el-form-item>
        </el-form>

        <el-button type="primary" round @click="handleUpdateAction"></el-button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';

    export default {
        setup () {
            const state = reactive ({
                pw : '',
                pw1 : '',
                pw2 : '',
                token : sessionStorage.getItem("TOKEN")
            });

            const handleUpdateAction = async () => {
                if(state.token !== null) { //  
                const url = `/member/updatepw`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                    };
                const body = {
                    password : state.pw,
                    password1: state.pw1
                    
                }
                const response = await axios.put(url, body, {headers:headers});
                console.log(response.data);
                if(response.data.status === 200){
                    alert(' ');
                }
            }
            
            else{
                //   
                // URL  
                //  /
                //   false
            }
        }

            return {state, handleUpdateAction}
        }
        
    }
</script>

