<template>
    <div>
        <h3> </h3>
        {{state}}
        <div v-if = "state.items">
            <img :src="state.items.imageUrl" style="width:300px;" /> <br />
             : {{state.items._id}} <br />
             : {{state.items.name}} <br />
             : {{state.items.content}} <br />
             : {{state.items.price}} <br />
             : <select v-model="state.items.ordercnt">
                <option v-for="no in 30" :key="no"> 
                <!-- Number(state.items.quantity)    -->
                {{ no }}
                </option>
            </select> <br />
            <button @click="handleOrderAciton"></button>
            <button @click="handleCartAciton"></button>
            <!-- <button @click="handleCartList"></button> -->
            
        </div>
        
    </div>
</template>

<script>
import { onMounted, reactive } from 'vue';
import { useRoute } from 'vue-router';
import { useRouter } from 'vue-router';
import axios from 'axios';

    export default {
        setup (){

            const route = useRoute();
            const router = useRouter();

            const state = reactive({
                code : route.query.code
            });

            // 
            const handleOrderAciton = async() => {
            router.push({name:"Order", 
                query : {
                    code: state.items._id, 
                    cnt : state.items.ordercnt
                }
            });
            }

            // 
            const handleCartAciton = async() => {
                const url = `/shop/insertcart`;
                const headers = {"Content-Type":"application/json"};
                // , ,    
                const body = {
                    code : state.items._id,
                    cnt : state.items.ordercnt
                }

                const response = await axios.post(url, body, {headers});
                console.log(response.data);
            }

            //  
            // const handleCartList = async () => {
            //     const url =`/shop/selectcart`;
            //     const headers = {"Content-Type":"application/json"};
            //     const response = await axios.get(url, {headers});
            //     console.log(response.data);

            // }

            onMounted( async()=> {
                const url = `/shop/selectone?code=${state.code}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                 if(response.data.status === 200){
                    state.items = response.data.result;
                    console.log(state.items)
                }
            })

            return {state, handleOrderAciton, handleCartAciton}

        }
    }
</script>

