<template>
    <div>
        <h3></h3>
        <input type="text" v-model="state.address" placeholder="" />
        <button @click="handleAddress"></button>
        <hr />

        <div v-if="state.items">
        <table border="1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(item,idx) in state.items" :key="item">
                <td>{{item.chk}}</td>
                <td><input type="text" v-model="item.address" /></td>
                <td>
                    <button @click="handleAddressUpdate(idx)"></button>
                    <button @click="handleAddressDelete(item._id)"></button>
                    <button @click="handleChkUpdates(item._id)"></button>
                </td>
            </tr>
        </table>
        </div>

    </div>
</template>

<script>
import { onMounted, reactive } from 'vue'
import axios from 'axios'
    export default {
        setup() {
            const state = reactive({
                address : '',
                token : sessionStorage.getItem("TOKEN"),
            })

            onMounted( () => {
                handleLoadData();
            })

            //  
            const handleLoadData = async() => {
                const url = `/member/selectaddr`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token,
                }
                const response = await axios.get(url, {headers});
                console.log('Menu5.vue => handleLoadData =>', response.data.result);
                if(response.data.status===200){
                    state.items=response.data.result;
                }               
            }

            //  
            const handleAddress = async() => {
                const url = `/member/insertaddr`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token,
                }
                const body = {
                    address : state.address
                }
                const response = await axios.post(url, body, {headers});
                if(response.data.status===200){
                    alert('.');
                    handleLoadData();
                }               
            }

            //  
            const handleAddressDelete = async(no) => {
                if(confirm(' ?')){
                    const url = `/member/deleteaddr`;
                    const headers = {
                        "Content-Type":"application/json",
                        "token" : state.token,
                    }
                    const body = { no : no };
                    const response = await axios.delete(url,
                        {headers:headers, data:body});
                    if(response.data.status===200){
                        alert('.');
                        handleLoadData();
                        }
                    }
                }

            //  
            const handleChkUpdates = async(no) => {
                if(confirm(' ?')){
                    const url = `/member/updatechkaddr`;
                    const headers = {
                        "Content-Type":"application/json",
                        "token" : state.token,
                    }
                    const body = {
                        no : no
                    }
                    const response = await axios.put(url, body, {headers:headers});
                    if(response.data.status===200){
                        alert('.');
                        handleLoadData();
                        }
                    }
                }

                //  
                //      idx
                const handleAddressUpdate = async(idx) => {
                    if(confirm(' ?')){
                    const url = `/member/updateaddr`;
                    const headers = {
                        "Content-Type":"application/json",
                        "token" : state.token,
                    }
                    const body = {
                        no : state.items[idx]._id,
                        address : state.items[idx].address
                    }
                    const response = await axios.put(url, body, {headers:headers});

                    if(response.data.status===200){
                        alert('.');
                        handleLoadData();
                        }
                    }
                }
    
        return {state, handleAddress, handleAddressDelete, handleChkUpdates, handleAddressUpdate}
        }
    }
</script>

