<template>
    <div class="style1">
        <h3></h3>
        {{ state }}
        <el-form :inline="false" :model="state" class="demo-form-inline" label-width="120px">
            <el-form-item label="()">
            <el-input v-model="state.userid" style="width:300px;" @keyup="handleEmailCheck" ></el-input>
            {{state.useremailcheck}}<br />
            <!-- <el-button type="primary" round @click="handleEmailCheck" style="margin-left:10px;"></el-button> -->
            </el-form-item>
            <el-form-item label="">
            <el-input v-model="state.userpw" style="width:300px;"></el-input>
            </el-form-item>
            <el-form-item label="">
            <el-input v-model="state.userpw1" style="width:300px;"></el-input>
            </el-form-item>
            <el-form-item label="">
            <el-input v-model="state.username" style="width:300px;"></el-input>
            </el-form-item>
            <el-form-item label="">
             <el-select v-model="state.userrole" class="m-2" placeholder="Select" size="large">
                <el-option value="Customer"></el-option>
                <el-option value="Seller"></el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-button type="primary" round @click="handleJoin"></el-button>
            </el-form-item>
        </el-form>
        
        <!--  :
        <select v-model="state.userrole">
            <option value="Customer"></option>
            <option value="Seller"></option>
        </select><br /> --> 
        
    </div>
</template>

<script>
import { reactive,ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';

export default {
    setup () {

        const router = useRouter();

        // High,   :   
        const state = reactive({
            userid : 'aaa',
            userpw : '',
            userpw1 : '',
            username : '',
            useremailcheck : '',
            userrole : '',
        });

        // Low,   :     
        //    ref  bbb    =  .
        const username = ref('null'); 
        const userpw = ref('null');
        const userid = ref('null');
        const userpw1 = ref('nul');

        //   
        const validEmail = (email) => {
            //  
            var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
            return re.test(email);
        }

        const handleEmailCheck = async() => {
            //         
            if(validEmail(state.userid)){
                console.log(state.userid);
                const url = `/member/emailcheck?email=${state.userid}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if(response.data.status===200){
                    if(response.data.result === 1){
                        state.useremailcheck='';
                    }
                    else{
                        state.useremailcheck='';
                    }
                }
            }
            else{
                state.useremailcheck='';
            }
        }

        // function handleJoin() { }, handleJoin 
        const handleJoin = async() => {
            if(state.userid === ''){
                alert(' ')
                userid.value.focus();
                return false; //    
            }
            if(state.userpw === ''){
                alert(' ')
                userpw.value.focus();
                return false;
            }
            if(state.userpw1 === ''){
                alert(' ')
                userpw1.value.focus();
                return false;
            }
            if(state.username === ''){
                alert(' ')
                username.value.focus();
                return false;
            }

            if(state.useremailcheck !== ''){
                alert(' .');
                userid.value.focus();
                return false;
            }
            //       

            const url = `/member/insert`;
            const headers = {"Content-Type":"application/json"};
            const body = { 
                email : state.userid, 
                password: state.userpw, 
                name: state.username,
                role : state.userrole
            };
            const response = await axios.post(url, body, {headers});
            console.log(response.data);
            if(response.data.status === 200) {
                alert('.');
                router.push({name:'Home'});
            }
        }
    
        return {state, username, userpw, userpw1, userid, handleJoin, handleEmailCheck}
    }
}
</script>




<!-- scss, less => css -->
<!-- cmd> npm install -D sass-loader@^10 sass -->
<style lang="scss" scoped>
    .style1 {
        border : 1px solid #cccccc;
        padding : 20px;
    }

</style>